*{
  margin:0;
  padding:0;
}
.welcome{
  height: 100vh;
  background-color: aquamarine;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.top{
  height: 19vh;
  display: flex;
  gap: 0.5%;
}
.board-container{
  overflow: hidden;
  width: 10%;
}
#board{
  height: 95%;
  width: 100%;
  /* margin-top: 100%; */
  background-color: yellow;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: larger;
  /* animation: board-arriving 3s forwards; */
  animation-delay: 1s;
}
@keyframes board-arriving {
  from{
    margin-top: 100%;
  }  
  to{
    margin-top: 0;
  }
}
#train{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 95%;
  width: 100%;
  background-color: rgb(110, 110, 233);
  border-radius: 80px 0 0 0;
  overflow: hidden;
  animation: train-arriving 4s ease-in-out forwards;
  transition: all 3s;
}
@keyframes train-arriving{
  from{
    margin-left: 100%; 
    overflow: hidden;
  }
  to{
    margin-left: 0;
    overflow: visible;
  }
}
#train-name{
  margin-left: 100%;
  animation: train-arriving 2s ease-out forwards;
  animation-delay: 2s;
  transition: all 2s;
}

#nav{
  display: flex;
  padding: 20px;
  transition: all 3s;
}
#nav ul{
  width: 100%;
  display: flex;
  justify-content: space-around;
  transition: all 3s;
}
#course-dropdown{
  color: yellow;
  text-align: center;
  text-decoration: underline;
  display: block;
  background-color: rgb(110, 110, 233);
  height: 20px;
  width: 80px;
  overflow: hidden;
  transition: all .5s;
  position: relative;
  z-index: 5;
}
#course-dropdown:hover{
  height: 186px;
  cursor: pointer;
}
#course-dropdown ul{
  display: block;
}
#course-dropdown ul li{
  margin: 5px;
}
a{
  color: yellow;
  font-weight: bold;
}
.obj-coding{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-image: url("bgimg.jpg");
  background-repeat: no-repeat;
}
.objective, .imp-link{
  width: 21vw;
  height: 65vh;
  background-color: rgb(2, 2, 58);
  color: yellow;
  overflow: hidden;
  padding: 5px;
}
marquee ul{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}
marquee ul li{
  text-decoration: underline;
}
.coder img{
  height: 66vh;
  animation: img-spin 4s infinite;
  animation-delay: 4s;
}
@keyframes img-spin {
  from{
    transform: rotate(0deg);
  }
  to{
    transform: rotate(360deg);
  }
}
.heading{
  height: 7vh;
  background-color: rgb(99, 99, 238);
  padding: 5px;
}
.message{
  height: 30vh;
  background-color: rgba(250, 246, 27, 0.897);
  color: black;
  font-size: 20px;
  padding: 10px;
  font-weight:450;
}
.footer{
  background-color: rgb(37, 37, 114);
  color: yellow;
  font-size: 20px;
  padding: 10px;
}
table{
  background-image: url("syllabus.jpg");
  background-size:  100%;
  background-repeat: no-repeat;
  font-weight: bold;
}
form{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 50vw;
  height: 70vh;
  margin: 30px auto;
  padding: 10px;
  background-color: rgb(81, 81, 233);
  gap: 10px;
}
form input, button{
  padding: 10px;
  border-radius: 5px;
  border-color: blue;
}
.payment-container{
  height: 61vh;
  background-color: black;
  position: relative;
}
.QRcode{
  margin-top: 0;
  margin-left: 100%;
  animation: qr 2s forwards;
  position: absolute;
}
@keyframes qr {
  0%{
    margin-top: 0;
    margin-left: 100%;
  }
  50%{
    margin-top: 0;
    margin-left: 45%;
  }
  100%{
    margin-top: 8%;
    margin-left: 45%;
  }
}
.nav ul{
  display: flex;
  background-color: hsl(209, 48%, 20%);
  justify-content: space-between;
  padding: 20px;
}
.userbody{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 52vh;
}
.fees-container{
  padding: 0 3rem;
  height: 80vh;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 10% 10%;
  overflow: auto;
  background-color: rgb(255, 255, 234);
}
.fees-card{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgb(233, 221, 221);
  /* background-color: rgb(252, 234, 212); */
  border-radius: 20px;
}
.faculty-body{
  background-color:brown;
  height: 62vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.faculty-img{
  border-radius: 50%;
  background-blend-mode: multiply;
}
.left{
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 50vw;
  background: linear-gradient(to right, green,transparent);
  color: white;
}
.right-container{
  width: 100%;
  height: 110px;
}
.right{
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: flex-end;
  width: 50vw;
  background: linear-gradient(to left,green,transparent);
  float: right;
  color: white;
}
@media screen and (max-width: 700px){
  .objective h1, .imp-link h1{
    font-size: 25px;
  }
  .objective, .imp-link{
    font-size: 20px;
  }
}
@media screen and (max-height:570px) {
  form{
    gap: 0;
  }
}
@media screen and (min-width:2210px) {
  .obj-coding{
    background-size: cover;
  }
  
}